.header
{
  text-align: center;
  color:deeppink;
}
*
{
  font-family: "Verdana", cursive, sans-serif;
  color: #ffffff;
  margin:0;
  padding:0
}
body{
  background: url("http://superdevresources.com/wp-content/uploads/2014/02/pattern-background.png");
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.app
{
  background: linear-gradient(5deg, #ff961d 0%, #f71aa2 100%);
  width: 95%;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;

}
.score-section {
  display: flex;
  font-size: 24px;
  align-items: center;
}
.question-section {
  width: 100%;
  position: relative;
}
.question-count
{
  margin-bottom: 20px;
}
.question-count span{
  font-size: 60px;
}
.question-text{
  margin-bottom: 12px;
}
.answer-section
{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}
.answer-section button{
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background: linear-gradient(83deg,#f71aa2 0%, #6800ff 80%);
  border-radius: 15px;
  display: flex;
  padding:10px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid #234668;
  cursor: pointer;
}